/* eslint-disable */
<template>
  <div>
    <Preloader v-if="loading" />
    <div v-else class="list-group">
      <div class="list-group-item" v-for="(item, idx) in statuslist" :key="item.state">
        <div class="d-flex">
          <img :src="item.icon" width="25" height="40" />
          <span class="ml-4">{{item.time}}</span>
          <div class="d-flex ml-auto align-items-center">
            <i class="fas fa-times" @click="removeItem(idx)"></i>
          </div>
        </div>
      </div>
    </div>

    <div class="btn-group-vertical">
      <button class="btn btn-primary" :disabled="btnOnStatus" @click="onMessage">{{ btnON }}</button>
      <button class="btn btn-danger" :disabled="btnOffStatus" @click="offMessage">{{ btnOFF }}</button>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import bus from "../event-bus";
import Preloader from "./Preloader.vue";
import FloatButton from "./FloatButton";

const fireData = firebase.database();
export default {
  data() {
    return {
      btnON: "開燈",
      btnOFF: "關燈",
      status: "off",
      statuslist: [],
      loading: false,
      icon: "",
      btnOnStatus: true,
      btnOffStatus: true,
      onFlag: false,
      offFlag: false
    };
  },
  components: {
    Preloader,
    FloatButton
  },
  methods: {
    removeItem(idx) {
      fireData.ref("myIOT").child(idx).remove();
    },
    onMessage() {
      let time = new Date()

      fireData.ref("LED_FLAG").set("ON");     

      fireData.ref("myIOT").push({
        status: "on",
        icon: "https://www.w3schools.com/js/pic_bulbon.gif",
        time: `${time}`
      });      
     
    },
    offMessage() {
      let time = new Date()

      fireData.ref("LED_FLAG").set("OFF");     

      fireData.ref("myIOT").push({
        status: "off",
        icon: "https://www.w3schools.com/js/pic_bulboff.gif",
        time: `${time}`
      });      
    }
  },
  created() {
    const vm = this;
    this.loading = true;

    bus.$on("setTab2Chinese", (on, off) => {
      this.btnON = on;
      this.btnOFF = off;
    });
    bus.$on("setTab2English", (on, off) => {
      this.btnON = on;
      this.btnOFF = off;
    });
    bus.$emit("item2");

    // setTimeout(() => {
      fireData.ref("myIOT").on("value", snapshot => {
        vm.statuslist = snapshot.val();
        vm.loading = false;
      });
    // }, 1500)

    fireData.ref("LED_RESULT").on("value", snapshot => {
      const { state } = snapshot.val();
      let time = new Date();
      if (state == 1) {
        vm.btnOnStatus = true;
        vm.btnOffStatus = false;
      }
      if (state == 0) {
        vm.btnOffStatus = true;
        vm.btnOnStatus = false;
      }
    });
  }
};
</script>

<style lang="scss" scoped>
.myBtn,
.btn {
  height: 40px;
  width: 80px;
  outline: none;
}

.btnOn,
.btn-primary {
  border-top-left-radius: 100px;
  border-top-right-radius: 100px;
  color: white;
}

.btnOff,
.btn-danger {
  border-bottom-left-radius: 100px;
  border-bottom-right-radius: 100px;
  color: white;
}

.btn-group-vertical {
  position: fixed;
  bottom: 30px;
  right: 30px;
  z-index: 1000;
}

.text-lg {
  font-size: 1.5em;
}
#iotList {
  position: relative;
  bottom: 50px;
}
</style>