import Vue from 'vue'
import VueRouter from 'vue-router'

// import TodoList from './components/TodoList.vue'
import ListGroup from './components/ListGroup.vue'
import ListGroupIOT from './components/ListGroupIOT.vue'

Vue.use(VueRouter)

export default new VueRouter({
  routes: [
    {
      path: '/',
      component: ListGroup
    },
    {
      path: '/todolist',
      component: ListGroup
    },
    {
      path: '/iot',
      component: ListGroupIOT
    }
  ]
})


