<template>
  <div
    class="modal fade"
    :id="myId"
    tabindex="-1"
    role="dialog"
    aria-labelledby="exampleModalLabel"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="exampleModalLabel">新增事項</h5>
          <button type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>

        <form>
          <div class="modal-body">
            <!-- <label for="addTodo">新增事項</label> -->
            <input
              id="addTodo"
              required
              type="text"
              class="form-control"
              v-model="todo"
              placeholder="Please enter your todo ..."

            />
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal" @click="cancel">取消</button>
            <button type="submit" class="btn btn-warning" :data-dismiss="data" @click="addTodo">確定</button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import bus from "../event-bus";
const todoRef = firebase.database().ref("myTodo")

export default {
  props: ["id"],
  data() {
    return {
      myId: this.id,
      todo: "",
      todos: [],
      data: ''
    };
  },
  methods: {
    cancel() {
      return;
    },
    emit() {
      bus.$emit("ok");
    },
    addTodo() {
      const stamp = new Date()
      if (this.todo == '') {
        return
      } else {
        /* vuejs */
        // this.todos.push({ 
        //   todo: this.todo         
        // })

        /* firebase */
        const timestamp = Math.floor(Date.now());
        todoRef.push({ 
          id: timestamp,
          todo: this.todo,        
        })

        /* localStorage */
        // localStorage.setItem('todos', JSON.stringify(this.todos))
        // const data = localStorage.getItem('todo')


        // close modal
        this.data = 'modal'
        this.todo = ''
      }
    }
  }
};
</script>

