<template>
  <div>
    <Preloader v-if="loading" />   
    
    <div v-else class="list-group">
      <div class="list-group-item pb-0" v-for="(item, idx) in todos" :key="item.todo" @dblclick="editTodo(item)">
        <div class="d-flex" v-if="item.id !== cache.id">
          <div class="mr-2" style="overflow: auto;white-space: nowrap; width: 60%">{{ item.todo }}</div>
          <div class="ml-auto btn-group" role="group">
            <button
              type="button"
              class="btn btn-sm btn-success"
              @click="editTodo(item)"
            >{{ btnEdit }}</button>                     
            <button
              type="button"
              class="btn btn-sm btn-danger ml-1"
              data-toggle="modal"
              data-target="#removeTodoModal"
              @click="removeTodo(idx)"
            >{{ btnDel }}</button>
          </div>
        </div>     
        <!-- <EditTodoModal id="edittodomodal"/> -->
       
        <div class="input-group" v-if="item.id === cache.id">
          <input type="text" class="form-control" v-model="cacheTodo" @keyup.esc="cancelEdit()" @keyup.enter="doneEdit(item)">   
          <div class="input-group-append ml-2">
            <button
                type="button"
                class="btn btn-sm btn-success"
                @click="editTodo(item)"
              >{{ btnEdit }}</button>
              <button
                type="button"
                class="btn btn-sm btn-danger ml-1"
                data-toggle="modal"
                data-target="todoModal"
                @click="removeTodo(idx)"
              >{{ btnDel }}</button>
          </div>
        </div>
      </div>
    </div>

    <FloatButton />
    <TodoModal id="todoModal" />
  </div>
</template>

<script>
import bus from "../event-bus";
import Preloader from "./Preloader.vue";
import TodoModal from "./TodoModal.vue";
// import EditTodoModal from "./EditTodoModal.vue";
import FloatButton from "./FloatButton.vue";

const todoRef = firebase.database().ref("myTodo");
// const localDB = JSON.parse(localStorage.getItem('todos') || '[]')

export default {
  components: {
    Preloader,
    TodoModal,    
    // EditTodoModal,    
    FloatButton
  },
  data() {
    return {
      todo: "",
      todos: [],
      loading: false,
      btnDel: "DEL",
      btnEdit: "EDIT",
      btnAdd: "ADD",
      cache: {}, // todos[]
      cacheTodo: "" // todo
    };
  },
  methods: {
    removeTodo(idx) {
      // list-group-item 加上背景色, 文字變白色
      // style="background: #aaa; color: #fff"

      const r = confirm(`您確定要刪除這個項目嗎 ?`);
      if (r == true) {
        todoRef.child(idx).remove();
      } else {
        return;
      }
    },
    editTodo(item) {
      this.cache = item;
      this.cacheTodo = item.todo;
      // alert(this.cacheTodo);
    },
    // addTodo() {
    //   if (this.todo == "") {
    //     alert("item cannot be empty !");
    //     return;
    //   } else {
    //     /* firebase */
    //     // todoRef.push({ todo: this.todo });
    //     // this.todo = "";

    //     /* vuejs */
    //     // this.todos.push({todo: this.todo})

    //     /* localStorage */
    //     localStorage.setItem('todos', { todo: this.todo })
    //     this.todo = "";
    //   }
    // }
    cancelEdit() {
      this.cache = {}
    },
    doneEdit(item) {
      item.todo = this.cacheTodo
      this.cacheTodo = ''
      this.cache = {}
    }
  },
  mounted() {
    bus.$on("addTodo", () => {});
  },
  created() {
    const vm = this;

    bus.$on("setTab1Chinese", (edit, del, add) => {
      this.btnDel = del;
      this.btnEdit = edit;
      this.btnAdd = add;
    });
    bus.$on("setTab1English", (edit, del, add) => {
      this.btnDel = del;
      this.btnEdit = edit;
      this.btnAdd = add;
    });
    bus.$emit("item1");
    this.loading = true;

    /* firebase */
    todoRef.on("value", function(snapshot) {     
      vm.todos = snapshot.val();
      vm.loading = false;

      localStorage.setItem("todos", JSON.stringify(snapshot.val()));
    });

    /* localStorage */    
    // const data = JSON.parse(localStorage.getItem('todos') || '[]')
    // vm.todos = data
    // vm.loading = false
  }
};
</script>

<style lang="scss" scoped>
.mouse-hover {
  cursor: pointer;
}

.list-group-item {
  font-size: 18px;
}

.highlight {
  background-color: #eee;
}
</style>