<template>
  <div id="app">
    <!-- <nav class="navbar navbar-expand-lg dark-bg">
      <span class="navbar-brand mb-0 h1 text-white">{{ brand }}</span>
      <div class="d-flex jusitify-content-end ml-auto">
        <div
          class="nav-link nav-item text-white cursor-pointer"
          :class="{primarybg: menuActive=='menuItem1'}"
          @click="getChinese"
        >{{ menuItem1 }}</div>
        <div
          class="nav-link nav-item text-white cursor-pointer"
          :class="{primarybg: menuActive=='menuItem2'}"
          @click="getEnglish"
        >{{ menuItem2 }}</div>
      </div>
    </nav>-->

    <nav class="navbar navbar-expand-lg dark-bg">
      <span class="navbar-brand mb-0 h1 text-white">{{ brand }}</span>
      <button
        class="navbar-toggler"
        type="button"
        data-toggle="collapse"
        data-target="#navbarNavAltMarkup"
        aria-controls="navbarNavAltMarkup"
        aria-expanded="false"
        aria-label="Toggle navigation"
      >
        <i class="fas fa-bars text-white"></i>
      </button>
      <div class="collapse navbar-collapse" id="navbarNavAltMarkup">
        <div class="navbar-nav text-center ml-auto">
          <div
            class="nav-link nav-item text-white cursor-pointer"
            :class="{primarybg: menuActive=='menuItem1'}"
            @click="getChinese"
          >{{ menuItem1 }}</div>
          <div
            class="nav-link nav-item text-white cursor-pointer"
            :class="{primarybg: menuActive=='menuItem2'}"
            @click="getEnglish"
          >{{ menuItem2 }}</div>        
        </div>
      </div>
    </nav>
    <!-- <AboutModal id="aboutModal" /> -->

    <div class="jumbotron jumbotron-fluid mb-0 pb-0 pt-4">
      <div class="container">
        <h1 class="display-5">{{title}}</h1>
        <div class="clearfix">
          <Preloader v-if="imgOK" />
          <img v-else class="img-fluid float-left mr-2" :src="image" width="50%" />
          <p class="lead" style="line-height: 1.2">{{subtitle}}</p>
        </div>
      </div>

      <!-- About the App -->
      <!-- <div class="p-3 mt-5 text-white mb-0 text-center" style="background: #272626;">
        <div>
          <i class="fas fa-tools mr-3"></i>
          <span>Best App</span>
        </div>
        <span style="font-size: 15px">Copyright © 2019 Stephen Laichaowen</span>
      </div>-->
    </div>

    <div class="d-flex bd-highlight text-center light-bg sticky-top" style="z-index: 500">
      <router-link
        class="p-2 flex-fill bd-highlight mouse-hover text-18"
        :class="{activated: visibility=='tab1'}"
        to="/todolist"
      >{{ tab1Text }}</router-link>
      <router-link
        class="p-2 flex-fill bd-highlight mouse-hover text-18"
        :class="{activated: visibility=='tab2'}"
        to="/iot"
      >{{ tab2Text }}</router-link>
    </div>
    <router-view />

    <div id="about" class="p-3 mt-1 text-white mb-0 text-center" style="background: #272626;">
      <div>
        <i class="fas fa-tools mr-3"></i>
        <span>Best App</span>
      </div>
      <span style="font-size: 15px">Copyright © 2019 Stephen Laichaowen</span>
    </div>
  </div>
</template>

<script>
import bus from "./event-bus";

export default {
  data() {
    return {
      brand: "實用 APP",
      menuItem1: "中文",
      menuItem2: "English",
      menuItem3: "About",
      title: "待辦項目",
      subtitle:
        "這是個人隨身日誌, 您可以增加, 修改, 刪除待辦事項, 幫助您隨時檢查行程, 是您貼身的好夥伴 ...",
      tab1Text: "待辦項目",
      tab2Text: "物聯網",
      image:
        "https://cdn.glitch.com/b8be080c-0b00-46d1-952d-ea65fe151eda%2F%E7%BB%84%201.png?v=1566977481262",
      visibility: "tab1",
      menuActive: "menuItem1",
      language: "chinese",
      tab1Status: "tab1Active",
      tab2Status: "tab2Active",
      imgOK: false
    };
  },
  methods: {
    showAboutBox() {
      alert("hi, there !!!");
    },
    showAlert() {
      alert("hi, there !");
    },
    getChinese() {
      this.language = "chinese";
      this.menuActive = "menuItem1";
      this.brand = "實用 APP";
      this.menuItem1 = "中文";
      this.menuItem2 = "English";
      this.tab1Text = "待辦項目";
      this.tab2Text = "物聯網";

      if (this.visibility == "tab1") {
        bus.$emit("setTab1Chinese", "修改", "刪除", "新增");
        this.title = "待辦項目";
        this.subtitle =
          "這是個人隨身日誌, 您可以增加, 修改, 刪除待辦事項, 幫助您隨時檢查行程, 是您貼身的好夥伴 ...";
        this.image =
          "https://cdn.glitch.com/b8be080c-0b00-46d1-952d-ea65fe151eda%2F%E7%BB%84%201.png?v=1566977481262";
      }

      if (this.visibility == "tab2") {
        bus.$emit("setTab2Chinese", "開燈", "關燈");
        bus.$emit("setTab2ListChinese", "開燈", "關燈");
        this.title = "物聯網";
        this.subtitle =
          "萬物皆可聯網, 現在您可以輕鬆的用手機遙控家裡的燈泡囉 ...";
        this.image =
          "https://cdn.glitch.com/e2d0de36-c372-4b42-8f8f-5dea2faa4285%2F%E7%BB%84%201.png?v=1566977449070";
      }
    },
    getEnglish() {
      this.language = "english";
      this.menuActive = "menuItem2";
      this.brand = "Best APP";
      this.menuItem1 = "中文";
      this.menuItem2 = "English";
      this.tab1Text = "TODO";
      this.tab2Text = "IOT";

      if (this.visibility == "tab1") {
        bus.$emit("setTab1English", "EDIT", "DEL", "ADD");
        this.title = "TodoList";
        this.subtitle =
          "This is your daily assistant, it lets you add, edit, remove todo items, help you manage your business any time/anywhere, it is your best buddy ...";
        this.image =
          "https://cdn.glitch.com/b8be080c-0b00-46d1-952d-ea65fe151eda%2F%E7%BB%84%201.png?v=1566977481262";
      }

      if (this.visibility == "tab2") {
        bus.$emit("setTab2English", "ON", "OFF");
        bus.$emit("setTab2ListEnglish", "ON", "OFF");
        this.title = "IOT";
        this.subtitle =
          "Everything can be connected to internet through wifi, now you can control homn appliances at your fingertips ...";
        this.image =
          "https://cdn.glitch.com/e2d0de36-c372-4b42-8f8f-5dea2faa4285%2F%E7%BB%84%201.png?v=1566977449070";
      }
    }
  },
  created() {
    bus.$on("item1", () => {
      this.visibility = "tab1";
      if (this.language == "chinese") {
        bus.$emit("setTab1Chinese", "修改", "刪除", "新增");
        this.title = "待辦項目";
        this.subtitle =
          "這是個人隨身日誌, 您可以增加, 修改, 刪除待辦事項, 幫助您隨時檢查行程, 是您貼身的好夥伴 ...";
        this.image =
          "https://cdn.glitch.com/b8be080c-0b00-46d1-952d-ea65fe151eda%2F%E7%BB%84%201.png?v=1566977481262";
      }
      if (this.language == "english") {
        bus.$emit("setTab1English", "EDIT", "DEL", "ADD");
        this.title = "TodoList";
        this.subtitle =
          "This is your daily assistant, it lets you add, edit, remove todo items, help you manage your business any time/anywhere, it is your best buddy ...";
        this.image =
          "https://cdn.glitch.com/b8be080c-0b00-46d1-952d-ea65fe151eda%2F%E7%BB%84%201.png?v=1566977481262";
      }
    });

    bus.$on("item2", () => {
      this.visibility = "tab2";
      if (this.language == "chinese") {
        bus.$emit("setTab2Chinese", "開燈", "關燈");
        this.title = "物聯網";
        this.subtitle =
          "萬物皆可聯網, 現在您可以輕鬆的用手機遙控家裡的燈泡囉 ...";
        this.image =
          "https://cdn.glitch.com/e2d0de36-c372-4b42-8f8f-5dea2faa4285%2F%E7%BB%84%201.png?v=1566977449070";
      }
      if (this.language == "english") {
        bus.$emit("setTab2English", "ON", "OFF");
        this.title = "IOT";
        this.subtitle =
          "Everything can be connected to internet through wifi, now you can control the bulb at your homn at snap ...";
        this.image =
          "https://cdn.glitch.com/e2d0de36-c372-4b42-8f8f-5dea2faa4285%2F%E7%BB%84%201.png?v=1566977449070";
      }
    });
  }
};
</script>

<style lang="scss" scoped>
