}

.primarybg {
  background-color: #c6c225;
}

router-link {
  text-decoration: none;
}

a:hover {
  color: red;
}

.activated {
  border-bottom: 4px solid #c6c225;
}

.cursor-pointer {
  cursor: pointer;
}

.mouse-hover {
  text-decoration: none;
  color: rgba(0, 0, 0, 0.75);
}

.text-18 {
  font-size: 18px;
  padding-bottom: 0;
}
</style>